import React from "react";
import { css } from "emotion";

export const Title = ({ level, children, className, ...rest }) => {
  switch (level) {
    case 1:
    default: {
      const defaultClassName = css`
        font-family: "Roboto", sans-serif;
        font-size: 50px;
        font-weight: bold;

        @media (max-width: 960px) {
          font-size: 30px;
        }
      `;
      return (
        <h1 className={`${defaultClassName} ${className}`} {...rest}>
          {children}
        </h1>
      );
    }
    case 2: {
      const defaultClassName = css`
        font-family: "Roboto", sans-serif;
        font-size: 32px;
        font-weight: bold;
        margin-bottom: 0;
        text-align: center;
      `;
      return (
        <h2 className={`${defaultClassName} ${className}`} {...rest}>
          {children}
        </h2>
      );
    }
    case 3: {
      const defaultClassName = css`
        font-family: "Source Sans Pro", sans-serif;
        font-size: 29px;
        font-weight: bold;
      `;
      return (
        <h3 className={`${defaultClassName} ${className}`} {...rest}>
          {children}
        </h3>
      );
    }
  }
};

export const Subtitle = ({ children, className = "", rest }) => {
  const defaultClassName = css`
    font-family: "Source Sans Pro", sans-serif;
    font-size: 15px;
    text-align: center;
  `;
  return (
    <h3 className={`${defaultClassName} ${className}`} {...rest}>
      {children}
    </h3>
  );
};

export const Text = ({ level, className, children, rest }) => {
  let defaultClassName = "";
  switch (level) {
    case 1:
    default:
      defaultClassName = css`
        font-family: "Source Sans Pro", sans-serif;
        font-size: 15px;
      `;
      break;
    case 2:
      defaultClassName = css`
        font-family: "Source Sans Pro", sans-serif;
        font-size: 12px;
      `;
      break;
  }
  return (
    <p className={`${defaultClassName} ${className}`} {...rest}>
      {children}
    </p>
  );
};
