import React from 'react';
import { css } from 'emotion';

import Logo from './Logo';

import GithubStars from './GithubStars';
import GithubIcon from './icons/Github';
import bp from './breakpoints';

const navBar = css`
    height: 90px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 40px;
`;

const menuClassName = css`
    height: 90px;
    padding-right: 25px;
    margin-right: 10px;
    display: flex;

    @media ${bp.mobile} {
        padding-right: 0;
        flex-direction: column;

        > div {
            width: 100vw;
            left: 0;
            top: 137px;
            display: block;
            position: absolute;
            z-index: 20;
            user-select: none;
            height: 0;
            transition: all 0.5s cubic-bezier(0.25, 0.8, 0.25, 1);
            overflow: hidden;
            background: linear-gradient(
                135deg,
                #00023b 0%,
                #00023b 50%,
                #313264 100%
            );
        }
        label {
            display: inline-block !important;
            margin: auto;
            align-self: flex-end;
            justify-self: flex-end;
            color: #fff;
            font-style: normal;
            font-size: 1.8em;
            padding: 10px;
        }
        input:checked ~ div {
            height: calc(100vh - 120px);
        }
    }

    label,
    #hamburger {
        display: none;
    }
`;

const menuLinkClassname = css`
    display: block;
    color: #fff;
    height: 50px;
    line-height: 50px;
    vertical-align: middle;
    margin-left: 50px;
    text-decoration: none;
    font-family: 'Roboto', sans-serif;
    font-size: 18px;
    transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);

    &:hover {
        color: #f0c070;
        svg {
            fill: #f0c070;
        }
    }

    svg {
        fill: white;
        vertical-align: middle;
        margin-right: 5px;
        height: 25px;
    }
`;

export default () => (
    <div className={navBar}>
        <a
            href="/"
            className={css`
                display: block;
                width: 70px;
                height: 70px;
                margin: 10px 10px 0 10px;
                justify-content: space-between;
                align-items: center;
            `}
        >
            <Logo style={{ height: 70 }} />
        </a>
        <div className={menuClassName}>
            <label
                htmlFor="hamburger"
                className={css`
                    font-family: auto;
                `}
            >
                &#9776;
            </label>
            <input type="checkbox" id="hamburger" />

            <div
                className={css`
                    display: flex;
                    align-items: center;
                    position: relative;
                `}
            >
                <a href="./Tutorial.html" className={menuLinkClassname}>
                    Documentation
                </a>
                <a
                    href="https://marmelab.com/react-admin-demo"
                    className={menuLinkClassname}
                >
                    Demo
                </a>
                <a
                    href="https://marmelab.com/en/blog/#react-admin"
                    className={menuLinkClassname}
                >
                    Blog
                </a>
                <a
                    href="https://github.com/marmelab/react-admin"
                    className={menuLinkClassname}
                >
                    <GithubIcon /> Github
                </a>
                <GithubStars
                    className={css`
                        margin-left: 20px;

                        @media ${bp.mobile} {
                            display: none;
                        }
                    `}
                    href="https://github.com/marmelab/react-admin"
                />
            </div>
        </div>
    </div>
);
