import React from "react";
import { css } from "emotion";

const containerClassname = css`
  max-width: 960px;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  align-items: stretch;
`;

export const Container = ({
  color,
  children,
  superClassName,
  className,
  ...rest
}) => {
  const superContainerClassname = css`
    width: 100%;
    background-color: ${color};
    padding: 30px 0;
  `;
  return (
    <div className={`${superContainerClassname} ${superClassName}`}>
      <div className={`${containerClassname} ${className}`}>{children}</div>
    </div>
  );
};

const subcontainerClassname = css`
  max-width: 960px;
`;
export const Subcontainer = ({ className, ...rest }) => (
  <div className={`${subcontainerClassname} ${className}`} {...rest} />
);
