import React, { useState, useEffect } from "react";
import { css } from "emotion";

const GITHUB_API_URL = "https://api.github.com/graphql";
const GITHUB_TOKEN = "b5e1278e07efd4c945d2c058ace85d04ef65be8d";

const GITHUB_QUERY = `query {
  stats: repository(owner:"marmelab" name:"react-admin"){
    stargazers{totalCount}
  }
}`;

const getGithubStats = () => {
  const headers = new Headers();
  headers.append("Authorization", `bearer ${GITHUB_TOKEN}`);
  headers.append("Content-Type", "application/x-www-form-urlencoded");

  const request = new Request(GITHUB_API_URL, {
    headers,
    method: "POST",
    body: JSON.stringify({ query: GITHUB_QUERY })
  });

  return fetch(request).then(response => response.json());
};

const Star = props => (
  <svg viewBox="0 0 500 625" {...props}>
    <path d="M382.4 307.1c-2.7 2.7-3.6 6.2-3.6 9.8l19.6 112.3c1.8 10.7-2.7 22.3-11.6 28.5s-20.5 7.1-31.2 2.7l-100.8-52.6c-3.6-1.8-7.1-1.8-10.7 0l-100.8 52.6c-4.5 2.7-8.9 3.6-13.4 3.6-6.2 0-11.6-1.8-16.9-5.3-8.9-6.2-13.4-17.8-11.6-28.5L121 317.8c.9-3.6-.9-7.1-3.6-9.8l-82-80.2c-8-8-10.7-19.6-7.1-30.3 3.6-10.7 12.5-17.8 23.2-19.6L164 160.8c3.6-.9 7.1-2.7 8.9-6.2l50.8-102.5C229 42.2 238.9 36 249.6 36c11.6 0 21.4 6.2 25.9 16.1l50.8 102.5c1.8 3.6 4.5 5.4 8.9 6.2l112.4 16c11.6 1.8 20.5 8.9 24.1 19.6 3.6 10.7.9 22.3-7.1 30.3l-82.2 80.4z" />
  </svg>
);

const linkClassname = css`
  border: 1px solid #f0c070;
  border-radius: 5px;
  background: none;
  font-size: 1.1em;
  color: #f0c070;
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  cursor: pointer;
  font-family: "Roboto", sans-serif;
  fill: #f0c070;
  text-decoration: none;
  &:hover {
    fill: white;
    color: white;
    background: #f0c070;
  }
  display: flex;
`;

const Stars = ({ className, ...rest }) => {
  const [stats, setStats] = useState({
    stars: 9300
  });

  useEffect(() => {
    getGithubStats().then(({ data }) => {
      setStats({
        stars: data.stats.stargazers.totalCount
      });
    });
  }, []);

  return (
    <a className={`${linkClassname} ${className}`} {...rest}>
      <div
        className={css`
          border-right: 1px solid #f0c070;
          padding: 2px 8px;
          height: 20px;
        `}
      >
        <Star style={{ height: 15, marginTop: 3 }} />
      </div>
      <div
        className={css`
          padding: 5px;
          font-size: 12px;
        `}
      >
        {stats.stars}
      </div>
    </a>
  );
};

export default Stars;
