import React from 'react';
import { Prism } from 'react-syntax-highlighter';
import { atomDark } from 'react-syntax-highlighter/dist/esm/styles/prism';
import { css } from 'emotion';

import { Container } from './Container';
import { Title, Text } from './Typography';
import { LIGHTGREY } from './colors';
import bp from './breakpoints';

const codeString = `import React from 'react';
import { Admin, Resource, ListGuesser } from 'react-admin';
import simpleRestProvider from 'ra-data-simple-rest';

const dataProvider = simpleRestProvider('https://domain.tld/api');

const App = () => (
    <Admin dataProvider={dataProvider}>
        <Resource name="users" list={ListGuesser} />
    </Admin>
);

export default App;`;

const bold = css`
    font-weight: bold;
`;

const SyntaxHighlighter = () => {
    const className = css`
        width: 45%;
        padding: 2em !important;
        font-size: 12px;
        margin-right: 3em;
        code > span {
            display: block;
            overflow: hidden;
            text-overflow: ellipsis;
        }
        @media ${bp.mobile} {
            font-size: 10px;
            width: 100%;
        }

        pre {
            display: flex !important;
            flex-direction: column !important;
            justify-content: center !important;
        }
    `;

    return (
        <Prism
            style={atomDark}
            wrapLines
            language="javascript"
            className={className}
        >
            {codeString}
        </Prism>
    );
};

const Preview = () => {
    const className = css`
        width: 45%;
        @media ${bp.mobile} {
            display: none;
        }
    `;
    return (
        <div className={className}>
            <a
                href="https://codesandbox.io/s/github/marmelab/react-admin/tree/master/examples/simple"
                title="Test simple example live"
            >
                <img
                    src="/assets/preview.png"
                    className={css`
                        width: 100%;
                        margin: 5px 0;
                    `}
                    alt="react-admin preview"
                />
            </a>
        </div>
    );
};

const EasyAsCake = () => (
    <Container
        color={LIGHTGREY}
        className={css`
            margin-bottom: -100px;
        `}
    >
        <Title
            level={2}
            className={css`
                color: black;
            `}
        >
            Easy as cake
        </Title>
        <Text
            level={1}
            className={css`
                text-align: center;
                padding-bottom: 2em;
        opacity: 0.5;
            `}
        >
            Build your first admin with{' '}
            <span className={bold}>13 lines of code</span>
        </Text>
        <div
            className={css`
                display: flex;
                justify-content: space-between;
                align-items: strech;
            `}
        >
            <SyntaxHighlighter />
            <Preview />
        </div>
    </Container>
);

export default EasyAsCake;
